import {mapGetters} from 'vuex'
export default {
  computed:{
    ...mapGetters({
      'getMainCategories':'category/getMainCategories'
    })
  },
  data(){
    return{
      colors:['red-ads','cyan-ads','blue-ads','orange-ads']
    }
  },
  methods:{
    randomClass(){
     let rnd = Math.floor(Math.random() * this.colors.length);
     return this.colors[rnd];
    },
    setMainCategory(item){
     this.$store.commit('ads/setNewAds',{key:'mainCategory',data:{id:item.id, name:item.name, validations:true}})
     setTimeout(() => {
       this.$router.push('/give-ads/categories')
     }, 250);

    },
  },
  mounted(){
    this.$store.commit('ads/setResetNewAds',{key:"mainCategory"})
  }
}
